<template>
  <div class="home">
    <div class="content">
      <!-- 图片区域  -->
      <div class="image-select">
        <div class="img-list">
          <div @click="imgClick(i.id,i.imageURL)" :class="{ active:currentImgId==i.id }" class="img-item" v-for="i in imageUrlList">
            <img class="img" :src="i.imageURL"/>
            <div class="upload-name">{{ i.name }}</div>
          </div>
        </div>
        <div @click="$refs.fileUpload.click();" class="upload">
          <img class="upload-img" src="./img/addImg.png"/>
          <div class="upload-name">上传图片</div>
          <input ref="fileUpload" accept="image/*" type="file" @change="uploadFileChange()" style="display: none"/>
        </div>
      </div>
      <!-- 面板区域   -->
      <div class="center">
        <div class="img-preview">
          <Viewer :hotSpots="currentHotSpots" :autoRotate="defaultConfig.autoRotate" ref="viewImage"></Viewer>
        </div>
      </div>
      <!-- 编辑属性面板  -->
      <div class="right-attribute">
        <el-form label-position="top" label-width="80px" :model="defaultConfig">
          <el-form-item>
            <el-switch :disabled="!currentImgId" v-model="defaultConfig.autoRotate" active-text="自动旋转"></el-switch>
          </el-form-item>
        </el-form>
        <el-button :disabled="!currentImgId" @click="addHotSpot(currentImgId)" size="mini" round>添加热点</el-button>
        <div v-for="item in currentHotSpots">
          <el-input v-model="item.text" placeholder="请输入内容"></el-input>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Viewer from "./Viewer";
export default {
  name: "EditPage",
  components: {Viewer},
  data(){
    return {
      funTabActive: 'global',// 当前活跃tab
      currentImgId: null,// 当前活跃
      imageUrlList: [],// 全景图列表
      defaultConfig: {
        pitch: 0,// （渲染）垂直俯仰角度（以度为单位）,默认0，范围[-90,90]
        yaw: 0,// （渲染）水平转动角度（以度为单位）,默认0，范围[-360, 360]
        hfov: 100,// （渲染）全景图的起始水平视野（以度为单位）,默认100,[50-120]
        autoRotate: false,// 自动旋转
      },
      hotSpots: { },// 热点（imgId:[]）
      currentHotSpots: [],
    }
  },
  methods:{
    // 功能操作切换
    funTabToggle(type){
      this.funTabActive = type;
    },
    // 上传全景图
    uploadFileChange() {
      let _this = this;
      const fileDom = this.$refs.fileUpload;
      let file = fileDom.files[0];// 获取文件
      fileDom.value ='';// 清空file
      if (file) {
        // 创建FileReader对象
        const reader = new FileReader();
        // 设置文件加载完成后的回调函数
        reader.onload = function() {
          const imageURL = reader.result; // 获取生成的URL
          // 可以将URL应用到页面中的img标签等元素
          let img = {
            id: _this.generateUUID(),
            imageURL,
            name: file.name
          };
          _this.imageUrlList.push(img);
          _this.imgClick(img.id,img.imageURL);
        };
        reader.readAsDataURL(file); // 读取文件并生成URL
      }
    },
    // 生成uuid
    generateUUID() {
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        const r = (Math.random() * 16) | 0,
            v = c === 'x' ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    },
    // 加载
    imgClick(id,imageURL){
      this.currentImgId = id;
      this.$refs.viewImage.loadImage(imageURL);
      this.currentHotSpots = this.hotSpots[id]?this.hotSpots[id]:[];
    },
    // 添加热点
    addHotSpot(imgId){
      const {pitch, yaw} = this.$refs.viewImage.config;
      if(this.hotSpots[imgId]){
        this.hotSpots[imgId].push({
          "id": this.generateUUID(),
          "pitch": pitch,
          "yaw": yaw,
          "text": "我是热点"
        });
      } else {
        this.hotSpots[imgId] = [];
        this.hotSpots[imgId].push({
          "id": this.generateUUID(),
          "pitch": pitch,
          "yaw": yaw,
          "text": "我是热点"
        });
      }
      this.currentHotSpots = this.hotSpots[imgId];
      console.log(this.currentHotSpots)
    }
  }
}
</script>

<style scoped lang="scss">
.home {
  height: 100%;
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
  //background-color: #f5efef;
  .content{
    height: 100%;
    width: 100%;
    border-radius: 5px;
    //background-color: #000000;
    overflow: hidden;
    display: flex;
    .image-select{
      width: 110px;
      display: flex;
      flex-direction: column;
      overflow: hidden;
      padding: 5px;
      box-sizing: border-box;
      background-color: #080808;
      .img-list{
        flex: 1;
        overflow: auto;
        display: flex;
        align-items: center;
        flex-direction: column;
        .img-item{
          width: 100px;
          height: 60px;
          border-radius: 5px;
          overflow: hidden;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          box-sizing: border-box;
          .img{
            width: 100%;
            height: 100%;
          }
          .upload-name{
            position: absolute;
            font-size: 12px;
            color: #ffffff;
          }
          &:not(:first-of-type){
            margin-top: 5px;
          }
        }
        .active{
          border: 2px solid #f50b0b;
        }
      }
      .upload{
        padding: 5px;
        border-radius: 5px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .upload-img{
          width: 50px;
          height: 50px;
        }
        .upload-name{
          font-size: 15px;
          color: #ffffff;
        }
        &:hover{
          background-color: #8aa2f3;
        }
      }
    }
    .center{
      flex: 1;
      display: flex;
      flex-direction: column;
      .img-preview{
        flex: 1;
        overflow: hidden;
        background-color: #feffff;
      }
      .function-select{
        display: flex;
        align-items: center;
        padding: 5px;
        box-sizing: border-box;
        background-color: #080808;
        .fun-item{
          padding: 5px;
          border-radius: 5px;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          .fun-img{
            width: 30px;
            height: 30px;
          }
          .fun-item-name{
            font-size: 12px;
            color: #ffffff;
          }
          &:not(:first-of-type){
            margin-left: 5px;
          }
        }
        .active{
          background-color: #8aa2f3;
        }
      }
    }
    .right-attribute{
      width: 110px;
      background-color: #0c0c0c;
      ::v-deep .el-switch__label{
        color: #ffffff;
      }
    }
  }
}
</style>